import React, { useState, useEffect } from "react";
import { withLDConsumer } from 'launchdarkly-react-client-sdk';
import useUserwayHook from '../hooks/useUserwayHook';
import logoImg from '../images/logo-img.png'
import circleImg from '../images/circle-124.png'
import circleSvg from '../images/circle-gray-20.svg'
import {
  GovBanner,
  MediaBlockBody,
  Header,
  Title,
  Footer,
  FooterNav,
  GridContainer,
  Grid,
  Logo,
  SocialLink,
  SocialLinks,
  Address,
  Form,
  Fieldset,
  Label,
  Table,
  TextInput,
  Checkbox,
  Button,
  Link,
  Identifier,
  IdentifierMasthead,
  IdentifierLogos,
  IdentifierLogo,
  IdentifierIdentity,
  IdentifierLinks,
  IdentifierLinkItem,
  IdentifierLink,
  IdentifierGov
} from "@trussworks/react-uswds";
import '@trussworks/react-uswds/lib/index.css';

function Application({ flags }) {
//useUserwayHook();
const [showPassword, setShowPassword] = React.useState(false)
const returnToTop = (
  <GridContainer className="usa-footer__return-to-top">
    <a href="#">Return to top</a>
  </GridContainer>
)

function ShowApplications() {
   const DisplayData=applications.map(
        (info)=>{
            return(
                <tr>
                  <td>{info.id}</td>
                  <td>{info.name}</td>
                  <td>{info.appStatus}</td>
                  <td>{info.publisher}</td>
                </tr>
            )
        }
    )
   return(
   <>
    <thead>
      <tr>
        <th scope="col">id</th>
        <th scope="col">Name</th>
        <th scope="col">Status</th>
        <th scope="col">Publisher</th>
      </tr>
    </thead>
    <tbody>
      {DisplayData}
    </tbody>
   </>
   )
}



const socialLinkItems = [
  <SocialLink key="facebook" name="Facebook" href="#" />,
  <SocialLink key="twitter" name="Twitter" href="#" />,
  <SocialLink key="youtube" name="YouTube" href="#" />,
  <SocialLink key="instagram" name="Instagram" href="#" />,
  <SocialLink key="rss" name="RSS" href="#" />,
]

const footerPrimary = (
  <FooterNav
    aria-label="Footer navigation"
    size="medium"
    links={Array(5).fill(
      <a href="javascript:void(0);" className="usa-footer__primary-link">
        Primary link
      </a>
    )}
  />
)

const footerSecondary = (
  <>
    <Grid row gap>
      <Logo
        size="medium"
        image={<img className="usa-footer__logo-img" src={logoImg} alt="" />}
        heading={<p className="usa-footer__logo-heading">Name of Agency</p>}
      />
      <Grid className="usa-footer__contact-links" mobileLg={{ col: 6 }}>
        <SocialLinks links={socialLinkItems} />
        <h3 className="usa-footer__contact-heading">Agency Contact Center</h3>
        <Address
          size="medium"
          items={[
            <a key="telephone" href="tel:1-800-555-5555">
              (800) CALL-GOVT
            </a>,
            <a key="email" href="mailto:info@agency.gov">
              info@agency.gov
            </a>,
          ]}
        />
      </Grid>
    </Grid>
  </>
)

const checkboxLabel = (
  <>
    I agree to the <a href="javascript:void(0);">terms and conditions</a>.{' '}
    <abbr title="required" className="usa-hint usa-hint--required">
      *
    </abbr>
  </>
)

const identifierLinksText = [
  'About <Parent shortname>',
  'Accessibility support',
  'FOIA requests',
  'No FEAR Act data',
  'Office of the Inspector General',
  'Performance reports',
  'Privacy policy',
]

const mockSubmit = (): void => {
  /* Blank function for mocking form submission */
}
const [error, setError] = useState(null);
const [isLoaded, setIsLoaded] = useState(false);
const [applications, setApplications] = useState({});
useEffect(() => {
    fetch(process.env.REACT_APP_FUNC_APP_URL)
       .then(res => res.json())
       .then(
          (data) => {
             fetch(process.env.REACT_APP_COSMOSDB_APP_URL)
                .then(res => res.json())
                .then(
                   (data) => {
                      console.log("Applications:",data);
                      setApplications(data);
                      setIsLoaded(true);
                   },
                   (error) => {
                      setIsLoaded(true);
                      setError(error);
                   }
                )
          },
          (error) => {
              setIsLoaded(true);
              setError(error);
          }
       )
}, [])
console.log("Toggle Flags are",flags);
var userwayScript;
if(flags.showUserway == true) {
  userwayScript = document.getElementById('userway_id');
  if(userwayScript == null) {
     userwayScript = document.createElement('script');
     userwayScript.id="userway_id"
     userwayScript.text = '(function(d){var s = d.createElement("script");s.setAttribute("data-account", "b7MauGmSkI");s.setAttribute("src", "https://accessibilityserver.org/widget.js");(d.body || d.head).appendChild(s);})(document)';
     userwayScript.async = true;
     document.body.appendChild(userwayScript);
  }
} else if(flags.showUserway == false) {
  userwayScript = document.getElementById('userway_id');
  console.log(userwayScript);
  if(!(userwayScript === undefined) && !(userwayScript == null)) {
	 document.body.removeChild(userwayScript);
  }
}
if (error) {
   return <div>Error: {error.message}</div>;
} else if (!isLoaded) {
   return <div>Loading...</div>;
} else {
   return (
     <>
      <a className="usa-skipnav" href="#main-content">
        Skip to main content
      </a>
      <GovBanner hidden={flags.showBanner ? false : true} />
      <Header extended>
        <div className="usa-navbar">
          <Title id="extended-logo">
            <a href="/" title="Home" aria-label="Home">
              ({flags.demoEnv}: PROJECT_ID)
            </a>
          </Title>
        </div>
      </Header>

      <main id="main-content">
        <div className="bg-base-lightest">
          <Table bordered caption="List of Applications">
            <ShowApplications/>
          </Table>
        </div>
      </main>

      <Footer
        returnToTop={returnToTop}
        primary={footerPrimary}
        secondary={footerSecondary}
      />
	  <Identifier>
        <IdentifierMasthead aria-label="Agency identifier">
          <IdentifierLogos>
            <IdentifierLogo href="#">
              <img
                className="usa-identifier__logo-img"
                src={circleSvg}
                alt="<Parent agency> logo"
              />
            </IdentifierLogo>
          </IdentifierLogos>
          <IdentifierIdentity domain="domain.gov">
            An official website of the <Link href="#">{`<Parent agency>`}</Link>
          </IdentifierIdentity>
        </IdentifierMasthead>
        <IdentifierLinks navProps={{ 'aria-label': 'Important links' }}>
          {identifierLinksText.map((text, idx) => (
            <IdentifierLinkItem key={idx}>
              <IdentifierLink href="#">{text}</IdentifierLink>
            </IdentifierLinkItem>
          ))}
        </IdentifierLinks>
        <IdentifierGov aria-label="U.S. government information and services">
          <div className="usa-identifier__usagov-description">
            Looking for U.S. government information and services?
          </div>
          &nbsp;
          <Link href="https://www.usa.gov/" className="usa-link">
            Visit USA.gov
          </Link>
        </IdentifierGov>
      </Identifier>
	</>
        );
    }
}
export default withLDConsumer()(Application);

