import { useEffect } from 'react';

const useUserwayHook = () => {
  useEffect(() => {
    const script = document.createElement('script');
    script.text = '(function(d){var s = d.createElement("script");s.setAttribute("data-account", "b7MauGmSkI");s.setAttribute("src", "https://accessibilityserver.org/widget.js");(d.body || d.head).appendChild(s);})(document)';
    script.async = true;
    document.body.appendChild(script);
    return () => {
      document.body.removeChild(script);
    }
  }, []);
};

export default useUserwayHook;